import React, { Component } from 'react';
import './index.less';
let logo = require('../../common/img/logo.png');
import { CSSTransition, TransitionGroup } from 'react-transition-group';
export default class HomeHeader extends Component {
    constructor() {
        super();
        this.state = { isShow: false, type: '' }
    }
    changeShow = () => {
        this.setState({ isShow: !this.state.isShow })
    }
    choose = (e) => {
        this.props.chooseLesson(e.target.type);
        this.changeShow();
        this.props.getLesson();
    }

    render() {
        return (
            <div className="home-header">
                <img src={logo} />
                <div className="home-menu" onTouchEnd={this.changeShow} >
                    { 
                        this.state.isShow ?
                            <i className="iconfont icon-guanbi"></i>
                            :
                            <i className="iconfont icon-shiliangzhinengduixiang"></i>
                    }
                </div>
                <TransitionGroup>
                    {this.state.isShow ?
                        <CSSTransition timeout={1000} classNames="fadeIn">
                            <ul className="menu-list" onTouchEnd={this.choose}>
                                <li type="all"></li>
                                <li type="computer"></li>
                                <li type="eco-management"></li>
                                <li type="psychology"></li>
                                <li type="foreign-language"></li>
                                <li type="literary-history"></li>
                                <li type="engineering"></li>
                                <li type="science"></li>
                                <li type="law"></li>
                                <li type="philosophy"></li>
                                <li type="teaching-method"></li>
                                <li type="biomedicine"></li>
                                <li type="art-design"></li>
                            </ul>
                        </CSSTransition>
                        :
                        null
                    }
                </TransitionGroup>
            </div>
        )
    }
}


