import * as Types from '../action-types';
import { regs,logins,auths,logouts } from '../../api/user';
import util from '../../common/util';
import {push} from 'react-router-redux';
// 
export const reg = (userInfo) => (dispatch) => {
    //console.log(userInfo);
    regs(userInfo).then(data => {
        //console.log(data);
        if (data.error) {
            dispatch({
                type: Types.SET_ERROR,
                error: data.error
            })
        } else {
            util.set('userInfo',data);//sessionStorage,
            dispatch({
                type: Types.SET_USER_INFO,
                userInfo: data,                
            })
            dispatch(push('/profile'));
        }
    })
}

// 
export const login=(userInfo)=>(dispatch)=>{
    logins(userInfo).then(data=>{
        //console.log(data);
        if(data.error){
            dispatch({
                type:Types.SET_ERROR,
                error:data.error
            })
        }else{
            util.set('userInfo',data);
            dispatch({
                type:Types.SET_USER_INFO,
                userInfo:data
            });
            dispatch(push('/profile'));//react-router-redux

        }
    })
}

// 
export const auth=()=>(dispatch)=>{
    auths().then(data=>{
        // data,{},{username,password...}
        if(data.username){
            util.set('userInfo',data);
            dispatch({
                type:Types.SET_USER_INFO,
                userInfo:data
            })
        }else{
            // 
            dispatch(push('/login'))
        }
    })
}

// 
export const logout=()=>(dispatch)=>{
    logouts().then(data=>{
        // data
        util.set('userInfo',data);
        dispatch({
            type:Types.SET_USER_LOGOUT_INFO,
            userInfo:data
        })
    })
}


