// fetch
let get=(url)=>{
    return fetch(url,{
        method:'get',//
        credentials:'include',//cookie
        headers:{
            accept:'application/json'//json
        }
    }).then(res=>res.json());
}
let post=(url,data)=>{
    return fetch(url,{
        method:'post',
        credentials:'include',
        headers:{
            "Content-Type":'application/json',
            accept:'application/json'
        },
        body:JSON.stringify(data),
    }).then(res=>res.json());
}
module.exports={get,post};


